@use '../../../Public/SCSS/Utilities/Mixins' as *;
@use '../../../Public/SCSS/Utilities/Variables' as *;

.application-viewport {
  @include SizeAndPositioning(relative, 100%, 80%, flex, column, nowrap, center, center, 0, 0, 0);
  background-color: rgba($colorOffWhite, 0.95);
  overflow-y: auto;
  .application-logo {
    @include SizingItem(relative, 40%, auto);
    margin-bottom: 3rem;
    transform: translateX(-14%);
  }
}
