@use '../../../../../Public/SCSS/Utilities/Variables' as *;
@use '../../../../../Public/SCSS/Utilities/Mixins' as *;

/////////////////////////////////
// Base

html {
  font-family: Purely Me, Century Gothic, Verdana, sans-serif;
  font-variant-ligatures: none;
}

////////////////////////////////////////////////
//  Master Grid Container
.r__master-grid-container {
  @include responsiveBreakPoint('smallMobilePort') {
    .r__app-landing-container {
      .r__logo-container {
        .r__logo-text {
          font-size: 4rem;
        }
      }
      .r__slogan {
        font-size: 2.4rem;
      }
    }

    .r__application-flex-container {
      .r__navigation {
        &--side-screen {
          &__section {
            &--user-information {
              &__username {
                font-size: 2rem;
              }
            }
            &--application-logo {
              &__text {
                font-size: 1.6rem;
              }
            }
          }
        }
      }
      .r__application-viewport {
        .r__application-display {
          .r__user-profile-section {
            .r__user-profile-closure-icon {
              font-size: 3rem;
            }
            &__header {
              &__text {
                font-size: 2.4rem;
              }
            }
            .r__form {
              &--full-width {
                .r__form__section {
                  .r__form {
                    &__input {
                    }

                    &__label {
                    }
                  }
                  &--switch {
                    .r__form {
                      &__input {
                      }
                      &__label {
                        &--comms {
                          display: flex;
                          justify-content: center;
                          text-align: center;
                        }
                      }
                    }
                  }
                  &--sub-section {
                    .r__form {
                      &__input {
                      }

                      &__label {
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
