@use '../../../../../Public/SCSS/Utilities/Variables' as *;
@use '../../../../../Public/SCSS/Utilities/Mixins' as *;

////////////////////////////////////////////////
//  BUDGET CARDS
.r__master-grid-container {
  .r__application-flex-container {
    .r__application-viewport {
      .r__application-display {
        .r__budget-card-container {
          @include responsiveBreakPoint('smallMobilePort') {
            @include SizeAndPositioning(relative, 70%, 100%, flex, row, nowrap, flex-start, center, 0, 0, 0);
            grid-row: 2 / span 15;
            overflow-x: auto;
            margin-bottom: 10rem;
            .fa-arrow-alt-circle-left,
            .fa-arrow-alt-circle-right {
              -webkit-text-stroke: 0.2rem $primaryColor;
              -moz-text-stroke: 0.2rem $primaryColor;
              transition: color 0.5s, -webkit-text-stroke 0.5s;
              &:hover {
                -webkit-text-stroke: 0.2rem $colorOffWhite;
                -moz-text-stroke: 0.2rem $colorOffWhite;
              }
              &:active {
                -webkit-text-stroke: 0.2rem $colorOffWhite;
                -moz-text-stroke: 0.2rem $colorOffWhite;
              }
            }
            &__card {
              @include SizeAndPositioning(relative, 100%, 20%, flex, column, nowrap, center, center, 0, 0, 0);
              border: 0.3rem solid rgba($primaryColor, 0.8);
              border-radius: 2.5rem;
              background-color: transparent;
              min-width: 28.4rem;
              margin: 0 1.5rem;
              &:first-of-type {
                margin-left: 3rem;
              }
              &:last-of-type {
                margin-right: 3rem;
              }
              &__header {
                @include SizeAndPositioning(absolute, 10%, 100%, flex, row, nowrap, center, center, 0, 0, 0);
                top: 0;
                border-top-left-radius: 2.1rem;
                border-top-right-radius: 2.1rem;
                background-color: rgba($primaryColor, 0.8);
                &__icon {
                  @include SizeAndPositioning(absolute, 3rem, auto, flex, row, nowrap, center, center, 0, -50%, 0);
                  top: 50%;
                  right: 2rem;
                  &:hover {
                    cursor: pointer;
                    transition: color 0.5s;
                  }
                }
              }
              &__content {
                @include SizeAndPositioning(absolute, 80%, 100%, flex, row, nowrap, center, center, 0, 0, 0);
                background-color: rgba($colorDarkestGrey, 0.1);
                opacity: 0.8;
                &__cover-photo {
                  @include SizeAndPositioning(relative, 100%, 100%, flex, row, nowrap, center, center, 0, 0, 0);
                }
                &__change-photo-button {
                  @include SizeAndPositioning(absolute, 4rem, 4rem, flex, row, nowrap, center, center, 0, 0, 0);
                  top: 4rem;
                  left: 4rem;
                  border: 0.25rem solid rgba($primaryColor, 0.8);
                  background-color: rgba($colorDarkestGrey, 0.25);
                  border-radius: 5rem;
                }
                &__permissions {
                  @include SizeAndPositioning(absolute, 5rem, 75%, flex, row, nowrap, space-evenly, center, -50%, 0, 0);
                  bottom: 0;
                  left: 50%;
                  background-color: rgba($colorOffWhite, 0.75);
                  border-top-left-radius: 1rem;
                  border-top-right-radius: 1rem;
                }
              }
              &__footer {
                @include SizeAndPositioning(absolute, 10%, 100%, flex, column, nowrap, center, center, 0, 0, 0);
                bottom: 0;
                border-bottom-left-radius: 2.1rem;
                border-bottom-right-radius: 2.1rem;
                background-color: rgba($primaryColor, 0.8);
                &--top {
                  @include SizeAndPositioning(relative, 100%, 100%, flex, column, nowrap, center, center, 0, 0, 0);
                  padding: 0 0.5rem;
                  &__first,
                  &__last {
                    height: 40%;
                  }
                }
              }
              &:hover {
                cursor: pointer;
                border: 0.3rem solid $primaryColor;
                transition: border 0.5s;
                .budget-card-container__card__header {
                  background-color: $primaryColor;
                  transition: background-color 0.5s, color 0.5s;
                  &__icon {
                    transition: color 0.5s;
                  }
                }
                .budget-card-container__card__content {
                  opacity: 1;
                  transition: opacity 0.5s;
                  &__change-photo-button {
                    &:hover {
                      cursor: pointer;
                      border: 0.25rem solid $primaryColor;
                      background-color: $primaryColor;
                      transition: border 0.5s, background-color 0.5s;
                      .budget-card-container__card__content__change-photo-button__icon {
                        transition: color 0.5s;
                      }
                    }
                  }
                }
                .budget-card-container__card__footer {
                  background-color: $primaryColor;
                  transition: background-color 0.5s, color 0.5s;
                }
              }
              &--create {
                @include SizeAndPositioning(relative, 100%, 20%, flex, column, nowrap, center, center, 0, 0, 0);
                border: 0.3rem solid rgba($primaryColor, 0.8);
                border-radius: 2.5rem;
                background-color: transparent;
                min-width: 28.4rem;
                &:last-of-type {
                  margin-left: 1.5rem;
                }
                &__header {
                  @include SizeAndPositioning(absolute, 10%, 100%, flex, row, nowrap, center, center, 0, 0, 0);
                  top: 0;
                  border-top-left-radius: 2.1rem;
                  border-top-right-radius: 2.1rem;
                  background-color: rgba($primaryColor, 0.8);
                }
                &:hover {
                  cursor: pointer;
                  border: 0.3rem solid $primaryColor;
                  transition: border 0.5s;
                  .budget-card-container__card--create__header {
                    background-color: $primaryColor;
                    transition: background-color 0.5s, color 0.5s;
                  }
                  .budget-card-container__card--create__icon {
                    transition: color 0.5s;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
